/* normalize.css v8.0.1 */
html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
}

main {
  display: block;
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dottedButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

*, *:before, *:after {
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.5rem;
  line-height: 1.5;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
}

h1, h2, h3, h4 {
  font-family: "Poppins", sans-serif;
  margin: 2rem 0;
}

h1 {
  font-size: 2.6rem;
}

h2 {
  font-size: 2.3rem;
}

h3 {
  font-size: 2rem;
}

h4 {
  font-size: 1.7rem;
}

p {
  font-size: 1.5rem;
  color: #000000;
  font-weight: 400;
  line-height: 1.5;
}

.text-center {
  text-align: center;
}

[class$=__heading] {
  font-family: "Poppins", sans-serif;
  margin: 2rem 0;
  margin: 2rem 0;
  text-align: center;
}

[class$=__descripcion] {
  text-align: center;
  font-size: 1.8rem;
  font-weight: 300;
  color: #000000;
  margin-bottom: 5rem;
}

.login {
  width: min(95%, 60rem);
  margin: 0 auto;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
  padding: 4rem 3rem;
  border-radius: 2rem;
  margin-top: 3rem;
}
.login__logo {
  font-weight: 900;
  display: flex;
  justify-content: center;
  font-size: clamp(3rem, -0.8rem + 10vw, 5rem);
  background: linear-gradient(to right, #10439F, #2b6fea);
  background-clip: text;
  color: transparent;
}
.login__nombre {
  font-family: "Poppins", sans-serif;
  margin: 2rem 0;
  margin: 2rem 0;
  text-align: center;
}

.formulario {
  display: flex;
  flex-direction: column;
}
.formulario__fieldset {
  border: none;
  margin-top: 1rem;
  padding: 0;
}
.formulario__legend {
  color: #5e5e5e;
  font-size: 2rem;
  margin-bottom: 1rem;
}
.formulario__campo {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-bottom: 1.3rem;
}
.formulario__label {
  display: block;
  color: rgba(0, 0, 0, 0.8);
  padding-left: 1rem;
  line-height: 1;
}
.formulario__input, .formulario__select {
  all: unset;
  padding: 0.8rem 1rem;
  border: 1px solid #20678f;
  border-radius: 0.5rem;
}
.formulario__submit {
  background-color: #10439F;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  border-radius: 1rem;
  display: inline-block;
  width: auto;
  align-self: flex-start;
  margin-top: 2rem;
}
.formulario__submit:hover {
  background-color: #092659;
  cursor: pointer;
}
.formulario__submit--registrar, .formulario__submit--full {
  width: 100%;
}

.alerta {
  padding: 1rem 1rem 1rem 2rem;
  margin: 1rem 0;
  font-weight: 700;
}
.alerta--error {
  background-color: #ffcccc;
  border-left: 0.5rem solid #990000;
  color: #990000;
}
.alerta--exito {
  background-color: #78f4c7;
  border-left: 0.5rem solid #0C9463;
  color: #043624;
}

.dashboard {
  height: 100%;
  display: flex;
  flex-direction: row;
  background-color: #ececec;
}
.dashboard [class$=__heading] {
  margin: 0 0 2rem 0;
  text-align: left;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 1rem;
}
.dashboard [class$=__heading]::before {
  font-family: "Font Awesome 6 Free";
  content: "\f08d";
  font-style: normal;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
}
.dashboard__grid {
  display: flex;
  flex-direction: column;
  flex: 1;
}
.dashboard__header {
  padding: 1.5rem 2rem;
  background-color: #081923;
}
.dashboard__contenido {
  padding: 1rem 2rem;
  width: 100%;
  overflow-y: scroll;
}
@media (min-width: 768px) {
  .dashboard__contenedor-boton {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 2rem;
  }
}
.dashboard__boton {
  background-color: #10439F;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  border-radius: 1rem;
  display: inline-block;
  font-size: 1.2rem;
  padding: 1rem 2rem;
}
.dashboard__boton:hover {
  background-color: #092659;
  cursor: pointer;
}
@media (min-width: 768px) {
  .dashboard__boton {
    width: auto;
  }
}
.dashboard__boton i {
  margin-right: 0.5rem;
}
.dashboard__contenedor, .dashboard__formulario {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
  background-color: #FFFFFF;
  padding: 2rem;
  border-radius: 1rem;
}
.dashboard__formulario {
  width: min(95%, 60rem);
  margin: 0 auto;
}

.sidebar {
  background-color: #07161f;
  width: 25rem;
  bottom: 0;
  overflow-y: scroll;
}
.sidebar__logo {
  color: #FFFFFF;
  font-weight: 700;
  padding: 1.7rem;
  text-align: center;
  font-size: 2rem;
  border-bottom: 2px solid #0b2533;
  margin-bottom: 3rem;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__menu {
  color: #FFFFFF;
  font-size: 2rem;
}
.header__menu:hover {
  cursor: pointer;
}
.header__perfil {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.header__informacion {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 1rem;
  color: #FFFFFF;
}
.header__informacion:hover {
  cursor: pointer;
}
.header__informacion::after {
  content: "";
  width: 0.6rem;
  height: 0.6rem;
  border-bottom: 2px solid #FFFFFF;
  border-right: 2px solid #FFFFFF;
  rotate: -45grad;
}
.header__icono {
  border: 1px solid #FFFFFF;
  border-radius: 50%;
  padding: 0.5rem;
}
.header__usuario {
  display: flex;
  flex-direction: column;
  font-weight: 500;
}
.header__usuario span {
  font-weight: 300;
  font-size: 0.8rem;
}
.header__config {
  display: none;
}

.menu {
  padding: 0;
  margin: 0;
  list-style: none;
  margin: 1rem;
}
.menu__item {
  margin-bottom: 2rem;
}
.menu__item--sub {
  margin-bottom: 1rem;
}
.menu__enlace {
  color: #999999;
  font-weight: 500;
  white-space: nowrap;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 1rem;
  gap: 1rem;
}
.menu__enlace:hover {
  cursor: pointer;
  color: #FFFFFF;
}
.menu__icono::after {
  content: "";
  width: 0.6rem;
  height: 0.6rem;
  border-bottom: 2px solid #FFFFFF;
  border-right: 2px solid #FFFFFF;
  rotate: -45grad;
  margin-right: 0.2rem;
}
.menu__nombre {
  flex-wrap: 1;
}
.menu__submenu {
  padding: 0;
  margin: 0;
  list-style: none;
  padding-left: 2rem;
  font-size: 1.2rem;
}

.table {
  width: 100%;
  border-collapse: collapse;
}
.table__thead {
  background-color: #123a51;
  color: #FFFFFF;
}
.table__th {
  padding: 1rem;
  text-align: left;
}
.table__tr:nth-child(even) {
  background-color: #e2e2e2;
}
.table__td, .table__td--acciones {
  padding: 1rem;
}
.table__td--acciones {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 1rem;
}
.table__td--estado {
  text-align: center;
}
.table__estado {
  font-size: 1.2rem;
  font-weight: 700;
}
.table__estado--activo {
  color: #064d34;
}
.table__estado--inactivo {
  color: #990000;
}
.table__accion {
  all: unset;
}
.table__accion--editar {
  color: #FF5B00;
}
.table__accion--estado {
  color: #10439F;
}
.table__accion--eliminar {
  color: #990000;
}
.table__accion:hover {
  cursor: pointer;
}

.paginacion {
  margin-top: 4rem;
  margin-bottom: 3rem;
  display: flex;
  gap: 0.7rem;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .paginacion {
    justify-content: flex-end;
  }
}
.paginacion__enlace {
  color: #10439F;
  font-size: 1.2rem;
}
.paginacion__enlace--texto {
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  border: 1px solid #10439F;
}
.paginacion__enlace--texto:hover {
  background-color: #10439F;
  color: #FFFFFF;
}
.paginacion__enlace--numero {
  font-weight: 700;
  border: 1px solid #10439F;
  padding: 0.2rem 1rem;
  border-radius: 0.5rem;
  display: none;
}
@media (min-width: 768px) {
  .paginacion__enlace--numero {
    display: inline;
  }
}
.paginacion__enlace--numero:hover {
  background-color: #10439F;
  color: #FFFFFF;
}
.paginacion__enlace--actual {
  color: #040D12;
  font-size: 1.6rem;
  font-weight: 700;
  display: none;
}
@media (min-width: 768px) {
  .paginacion__enlace--actual {
    display: inline;
  }
}